/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-snap-type: y mandatory;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 16px;
  background: radial-gradient(circle, #0044aa 50%, #1c1f1c 100%);
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 3rem auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.5rem;
}
.contenedor .contenedor-check {
  display: flex;
  gap: 4rem;
}
.contenedor .costos {
  max-width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenedor .costos label {
  font-size: 2.4rem;
  color: #ffc107;
  margin-bottom: 1rem;
}
.contenedor .costos input {
  background-color: #ffffff;
  border-radius: 1rem;
  padding: 0.5rem;
  text-align: center;
  color: #1c1f1c;
  font-weight: 700;
  font-size: 2rem;
}
.contenedor .check {
  display: flex;
  gap: 1rem;
}
.contenedor .check label {
  font-size: 2rem;
  color: #ffffff;
  margin-bottom: 1rem;
}
.contenedor .check input {
  width: 2rem;
  height: 2rem;
}
.contenedor .check input[type=checkbox]:checked + label {
  color: #babdc2;
  font-size: 2.4rem;
}
.contenedor .botones {
  width: 90%;
}
@media (min-width: 410px) {
  .contenedor .botones {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.contenedor .botones input {
  background-color: #1c1f1c;
  text-decoration: none;
  color: #0044aa;
  text-transform: uppercase;
  font-weight: 900;
  padding: 1rem 1rem;
  width: 100%;
  text-align: center;
  border-radius: 1rem;
  border: 1px solid #ffffff;
  margin-bottom: 1rem;
}
.contenedor .botones input:hover {
  background-color: #040404;
  cursor: pointer;
}
.contenedor .promos {
  display: block;
  max-width: 60%;
}

.cuotas {
  background-color: transparent;
  font-size: 2rem;
  margin-top: 2.5rem;
  color: #d5d7da;
}

.whatsapp {
  display: block;
  background-color: #1c1f1c;
  text-decoration: none;
  color: #0044aa;
  text-transform: uppercase;
  font-weight: 900;
  padding: 1rem 1rem;
  width: 100%;
  text-align: center;
  border-radius: 1rem;
  border: 1px solid #ffffff;
  max-width: 95%;
  font-size: 2.4rem;
  padding: 0.5rem;
}
.whatsapp:hover {
  background-color: #040404;
  cursor: pointer;
}

.titulos {
  margin-top: 5rem;
}

h1 {
  font-size: 3rem;
  color: #ffffff;
  margin-bottom: 1rem;
}

h2 {
  margin: 0;
  font-size: 2.6rem;
  color: #babdc2;
}

h3 {
  font-size: 1.8rem;
}

h1, h2 {
  text-align: center;
}

.sombra {
  -webkit-box-shadow: 0px 0px 20px 7px rgb(186, 189, 194);
  -moz-box-shadow: 0px 0px 20px 7px rgb(186, 189, 194);
  box-shadow: 0px 0px 20px 7px rgb(186, 189, 194);
  padding: 2rem;
  border-radius: 2rem;
}

.imgPreview {
  height: 12rem;
  border-radius: 25%;
}

